import { html } from 'lit';

// Import Shoelace components
import '@shoelace-style/shoelace/dist/components/dialog/dialog.js';
import '@shoelace-style/shoelace/dist/components/button/button.js';
import '@shoelace-style/shoelace/dist/components/icon-button/icon-button.js';
import '@shoelace-style/shoelace/dist/components/input/input.js';
import '@shoelace-style/shoelace/dist/components/rating/rating.js';

const meta = {
  title: 'Shoelace/Overlays/Dialog',
  tags: ['autodocs'],
  render: (args) => html`
    <div>
      <sl-dialog
        ?open=${args.open}
        ?no-header=${args.noHeader}
        label=${args.label}
        style=${args.customWidth ? '--width: 50vw;' : ''}
      >
        ${args.headerActions ? html`
          <sl-icon-button
            slot="header-actions"
            name="box-arrow-up-right"
            label="Open in new window"
          ></sl-icon-button>
        ` : ''}

        ${args.content}
        
        ${args.footer ? html`
          <div slot="footer">
            ${args.footer}
          </div>
        ` : ''}
      </sl-dialog>

      <sl-button @click=${args.openDialog}>Open Dialog</sl-button>
    </div>
  `,
  argTypes: {
    open: { control: 'boolean' },
    noHeader: { control: 'boolean' },
    label: { control: 'text' },
    content: { control: 'text' },
    headerActions: { control: 'boolean' },
    customWidth: { control: 'boolean' },
    footer: { control: 'text' }
  }
};

export default meta;

// Basic dialog
export const Basic = {
  args: {
    label: 'Dialog',
    content: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit.',
    footer: html`<sl-button variant="primary">Close</sl-button>`,
    openDialog: (e) => {
      const dialog = e.target.previousElementSibling;
      dialog.show();
    }
  }
};

